.Experiences {
  display: flex;
}
.Experiences.background4 {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(7, 7, 92);
  width: 100%;
  height: 100%;
}
.Experiences.title {
  justify-content: center;
  font-family: "GangwonEduPowerExtraBoldA";
  color: white;
  font-size: 40px;
  margin-bottom: 0;
}
.Experiences.divider.small {
  width: 3.25rem;
  height: 0;
  border: none;
  border-top: 0.2rem solid #ffffff;
  opacity: 1;
}

.BGtitle {
  width: 100%;
  text-align: left;
  margin-left: 2rem;
  font-size: 25px;
}

.whiteBG {
  font-family: "GangwonEduPowerExtraBoldA";
