  flex-direction: column;
  width: 1250px;
  height: 600px;
  background-color: white;
  margin: 1rem;
  margin-bottom: 4rem;
  border-radius: 1rem;
  -webkit-box-shadow: 1rem 1rem 1rem 0 rgba(139, 139, 139, 0.2);
  padding-bottom: 1rem;
}

.insideBG {
  width: 1150px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.insideBGright {
